<template>

  <el-row :gutter="20">

    <el-col :span="18" :offset="3" v-for="post in posts" :key="post.id">
      <el-card :body-style="{ padding: '0px' }" shadow="hover" style="martin-top: 30px; margin-bottom: 30px">
        <div style="padding: 14px">
        <img v-if="post.image && post.image != ''" style="float: right; margin-bottom:auto; width: 250px" :src="post.image"/>
        <el-image v-else style="float: right; margin-bottom:auto; width: 250px" src="https://pineco.de/wp-content/uploads/2018/10/laravel-package-development-basics.png"></el-image>
          <p class="text-center">
            <b>{{ post.title }}</b>
          </p>
          <div class="bottom">
            {{ post.summary }}
            <el-button type="text" class="button">Read More</el-button>
            <div style="float: right; margin-top: 5px; margin-bottom: 5px">
               <span > {{ timeConvert(post.created_at) }}</span> 
            <!--<el-avatar :size="40" src="https://empty"  @error="errorHandler" >
              <img src="../../../public/pp-img2.jpg"/>
            </el-avatar>-->
            </div>
          </div>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
import { useLoadPosts } from '@/firebase'
export default {
  setup() {
    const posts = useLoadPosts()

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return curr_date + "-" + curr_month + "-" + curr_year
    }

    return {
      posts,
      timeConvert
    }
  }

}
</script>

