<template>
  <el-container>
    <el-header class="text-center" style="height: auto; padding: 0px">
      <layout></layout>
    </el-header>
    <el-main>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="grid-content bg-purple">
            <h3 class="text-center">Login<!--Register--></h3>
            <el-form :model="form" label-width="120px">
              <el-form-item
                prop="email"
                label="Email"
                :rules="[
                    {
                    required: true,
                    message: 'Please input email address',
                    trigger: 'blur',
                    },
                    {
                    type: 'email',
                    message: 'Please input correct email address',
                    trigger: ['blur', 'change'],
                    },
                ]"
              >
                <el-input v-model="form.email" />
              </el-form-item>
              <el-form-item 
                label="Password" 
                prop="password"
                :rules="[
                    {
                    required: true,
                    message: 'Please input password',
                    trigger: 'blur',
                    },
                ]"
              >
                <el-input v-model="form.password" type="password" autocomplete="off" />
              </el-form-item>
              <el-form-item >
                <el-button type="primary" @click="onSubmit">Login</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </el-main>
    <el-footer class="text-center" style="background-color: #a66081;height: auto; padding: 5px">
      <small style="color: white"><b>©2022 Gülşah Alıcı</b></small>
    </el-footer>
  </el-container>
  <div>
  </div>
</template>

<script>
import Layout from './Layout.vue'
import { reactive, computed, watch } from 'vue'
//import { createUser } from '@/firebase'
import { loginUser } from '@/firebase'
import { ElMessage } from 'element-plus'
import { useRouter } from 'vue-router'

export default {
  components: { Layout },
  props: {
    admin: Object
  },
  setup(props) {
    const adminGulsah = computed(() => {
      return props.admin
    })
    const form = reactive({
      email: '',
      password: ''
    })

    const router = useRouter()

    const onSubmit = async () => {
      /*
      Create user
      await createUser(form)
       
      form.email = ''
      form.password = ''
      */
     await loginUser(form)
      .then(data => {
        ElMessage({
          message: 'Welcome ' + data.user.email.split('@')[0] + ' !',
          type: 'success',
        })
        
        setTimeout(() => router.push({path: '/admin' }), 2000)
      })
      .catch(err => {
        ElMessage({
          message: err.message,
          type: 'error',
        })
      })

    }
    watch(adminGulsah, (val) => {
      if(val) {
        router.push({path: '/admin' })
      }
    })
  

    return {
      form,
      onSubmit
    }
  }
}
</script>

