<template>
  <el-container>
    <el-header class="text-center" style="height: auto; padding: 0px">
      <layout></layout>
    </el-header>
    <el-main style="height: auto">
      <el-row>
        <el-col :span="22">
        </el-col>
        <el-col :span="2"><el-button :plain="true" @click="logout">Logout</el-button></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="16" :offset="4">
          <div class="grid-content bg-purple">
            <h3 class="text-center">New Post</h3>
            <el-form :model="form" label-width="120px">
              <el-form-item label="Title">
                <el-input v-model="form.title" />
              </el-form-item>
              <el-form-item label="Summary">
                <el-input v-model="form.summary" :autosize="{ minRows: 2, maxRows: 4 }" type="textarea" placeholder="Please input" />
              </el-form-item>
              <el-form-item label="Created at">
                <el-date-picker v-model="form.created_at" type="date" placeholder="Pick a date" style="width: 100%" />
              </el-form-item>
              <el-form-item label="Image">
                <el-upload action="#" list-type="picture-card" :auto-upload="false" @change="uploadImg">
                  <template #default>
                    <el-icon><Plus /></el-icon>
                  </template>
                  <template #file="{ file }">
                    <div>
                      <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
                      <span class="el-upload-list__item-actions">
                        <span
                          class="el-upload-list__item-preview"
                          @click="handlePictureCardPreview(file)"
                        >
                          <el-icon><zoom-in /></el-icon>
                        </span>
                        <span
                          v-if="!disabled"
                          class="el-upload-list__item-delete"
                          @click="handleDownload(file)"
                        >
                          <el-icon><Download /></el-icon>
                        </span>
                        <span
                          v-if="!disabled"
                          class="el-upload-list__item-delete"
                          @click="handleRemove(file)"
                        >
                          <el-icon><Delete /></el-icon>
                        </span>
                      </span>
                    </div>
                  </template>
                </el-upload>
              </el-form-item>
              <el-form-item label="Content">
                <div>
                  <QuillEditor v-model:content="form.content" theme="snow" contentType="html" toolbar="full" ref="quillEditor" style="min-height: 200px"/>
                </div>
              </el-form-item>
              <el-form-item label="Author">
                <el-input v-model="form.author" />
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">Create Post</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </el-main>
    <el-footer class="text-center" style="background-color: #a66081;height: auto; padding: 5px">
      <small style="color: white"><b>©2022 Gülşah Alıcı</b></small>
    </el-footer>
  </el-container>
  <div>
  </div>
</template>

<script>
import Layout from './Layout.vue'
import { computed, onBeforeMount, reactive } from 'vue'
import { useRouter } from 'vue-router'
import firebase from 'firebase'
import { ElMessage } from 'element-plus'
import { Plus, ZoomIn, Download, Delete } from '@element-plus/icons-vue'
import { createPost, useLoadPosts } from '@/firebase'

export default {
  components: { Layout, Plus, ZoomIn, Download, Delete },
  props: {
    admin: Object
  },
  setup(props) {

    const router = useRouter()
    const posts = useLoadPosts()

    const form = reactive({
      title: '',
      summary: '',
      image: '',
      created_at: '',
      content: '',
      author: 'Gülşah Alıcı'
    })

    const onSubmit = async () => {
      await createPost(form)
        .then(() => {
          ElMessage({
            message: 'Post saved!',
            type: 'success',
          })
        })
        .catch(err => {
          ElMessage({
            message: err.message,
            type: 'error',
          })
        })
    }

    const adminGulsah = computed(() => {
      return props.admin
    })

    const logout = () => {
      firebase.auth().signOut()
        .then(() => {
          ElMessage({
            message: 'Logged out!',
            type: 'success',
          })
          router.push({path: '/admin/login' })
        })
        .catch(err => {
          ElMessage({
            message: err.message,
            type: 'error',
          })
        })
    }

    const handleRemove = (file) => {
      console.log(file)
    }

    const uploadImg = (event) => {
      let reader = new FileReader();
      let file = event.raw;
      reader.readAsDataURL(file);
      reader.onload = function(){
        form.image = reader.result
      }
    }

    onBeforeMount(() => {
      if(!adminGulsah.value) {
        router.push({path: '/admin/login' })
      }
    })

    return {
      logout,
      adminGulsah,
      onSubmit,
      form,
      handleRemove,
      posts,
      uploadImg
    }
  }
}
</script>

